import { Component, OnInit } from '@angular/core';
import { Router } from "@angular/router";


import { PixelMeService } from '.././pixel-me.service';

@Component({
  selector: 'app-enter-api-key',
  templateUrl: './enter-api-key.component.html',
  styleUrls: ['./enter-api-key.component.css']
})
export class EnterApiKeyComponent implements OnInit {

  apiToken: string = '';
  apiTokenFilled: boolean;
  apiTokenValid: boolean;

  constructor(private router: Router,
    private service: PixelMeService) {
    this.apiTokenFilled = false;
    this.apiTokenValid = true;
  }

  ngOnInit() {

  }

  onGetStarted() {
    this.service.setApiKey(this.apiToken);

    this.service.getAccountResponseStatus((data => {
      if (data == null) {
        this.apiTokenValid = false;
        this.service.setApiKey('');        
      } else if (data == 401) {
        this.apiTokenValid = false;
        this.service.setApiKey('');
      } else if (data == 404) {
        this.router.navigate(['trialEnd']); // 404 is trial ended
      } else {
        this.service.setApiKey(this.apiToken);
        this.router.navigate(['app']); // route to settings
      }
    }));
  }

  onApiKeyEnter(event: any) {
    this.apiToken = event.target.value;
    if (typeof this.apiToken != 'undefined' && this.apiToken)
      this.apiTokenFilled = true;
    else
      this.apiTokenFilled = false;
  }

}



