import { Component, OnInit } from '@angular/core';
import { Router } from "@angular/router";

import { User } from './../user';
import { Pixel } from './../pixel';

import { PixelMeService } from '.././pixel-me.service';
declare var $: any;
@Component({
  selector: 'app-link-creator',
  templateUrl: './link-creator.component.html',
  styleUrls: ['./link-creator.component.css']
})
export class LinkCreatorComponent implements OnInit {

  linkToShorten: string = "";
  shortedLink: string = "";
  shortedTitle: string = "";
  linkId: string = '';
  linkDomain: string = '';
  linkEnding: string = '';

  pixels: Pixel[] = [];
  domains: string[] = [];
  pixelsSelected: Pixel[] = [];
  domainSelected: string = "";

  loaded: boolean = false;
  uiInitialized: boolean = false;
  created: boolean = false; // if link created - for ui refresh
  isValidLinkToShorten: boolean = false;
  edit: boolean = false;
  error: boolean = false;

  constructor(private router: Router,
    private service: PixelMeService) { }

  ngOnInit() {
    this.initializeDropdown();
    this.pixels = this.service.getPixels();
    this.domains = this.service.getDomains();
    this.pixelsSelected = this.service.getSelectedPixels();
    this.domainSelected = this.service.getSelectedDomain();
    this.linkDomain = this.domainSelected;

    this.loaded = true;
  }

  // Called after the ngAfterViewInit and every subsequent ngAfterContentChecked().
  ngAfterViewChecked() {
    if (this.loaded == true && this.uiInitialized == false) {
      this.initializeDropdown();
      this.uiInitialized = true;
    }
  }


  OnInputLinkToShorten() {
    var urtlPattern = new RegExp(/^(http:\/\/www\.|https:\/\/www\.|http:\/\/|https:\/\/)[a-z0-9]+([\-\.]{1}[a-z0-9]+)*\.[a-z]{2,5}(:[0-9]{1,5})?(\/.*)?$/);
    this.isValidLinkToShorten = urtlPattern.test(this.linkToShorten);
  }

  onPixelizeMe() {
    this.service.getShortLinkData(this.linkToShorten, (data => {
      
      // checking errors
      if (data.error != null) {
        // checking trial
        if (data.error.error_message.startsWith('Trial ended')) {
          this.router.navigate(['trialEnd']);            
        } else {
          this.router.navigate(['error'], { queryParams: { errorMessage: data.error.error_message } });
        }
      }

      this.linkId = data.id; // for editing link ending
      this.shortedLink = data.shorten;
      this.shortedTitle = data.title;
      this.loaded = true;
      this.created = true;
      this.copyToClipboard(this.shortedLink);
    }));
  }

  // Handling domain selecting in dropdown
  onDomainSelected(event: any) {
    this.domainSelected = event;
  }

  // Handling pixel selecting in dropdown
  onPixelSelected(pixel: Pixel) {
    var idx = this.pixelsArrayIndex(this.pixelsSelected, pixel);

    // Is currently selected
    if (idx > -1) {
      this.pixelsSelected.splice(idx, 1);
    }
    // Is newly selected
    else {
      this.pixelsSelected.push(pixel);
    }
  };

  // when called, will initialize all dropdown and all checkboxes.
  initializeDropdown() {
    $('.ui.selection.dropdown').dropdown();
    $('.ui.checkbox').checkbox();
    $('#n').dropdown({ action: 'nothing' });
  }

  pixelsArrayContains(arr: Pixel[], px: Pixel) {
    return this.service.pixelsArrayContains(arr, px);
  }

  pixelsArrayIndex(arr: Pixel[], px: Pixel): number {
    return this.service.pixelsArrayIndex(arr, px);
  }

  copyToClipboard(text) {
    var textArea = document.createElement("textarea");
    textArea.value = text;
    document.body.appendChild(textArea);

    textArea.select();

    try {
      var successful = document.execCommand('copy');
      var msg = successful ? 'successful' : 'unsuccessful';
      console.log('Copying text command was ' + msg);
    } catch (err) {
      console.log('Oops, unable to copy');
    }

    document.body.removeChild(textArea);
  }


  onEditSave() {
    // if error was when last attempt
    this.error = false;

    this.service.editShortLink(this.linkId, this.linkEnding, (data) => {

      this.loaded = false;

      // checking errors
      if (data.error != null) {
        // checking trial
        if (data.error.error_message.startsWith('Trial ended')) {
          this.router.navigate(['trialEnd']);            
        } else {
          this.router.navigate(['error'], { queryParams: { errorMessage: data.error.error_message } });
        }
      }
      
      // checking input errors
      if (data.error != null && data.error.error_message.startsWith('Key already taken for this domain')) {
        this.error = true;
        this.loaded = true;
      } else {
        this.shortedLink = data.shorten;
        this.copyToClipboard(this.shortedLink);
        this.edit = false;
        this.loaded = true;
      }


    })
  }


}



