import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AppComponent } from './app.component';
import { EnterApiKeyComponent } from './enter-api-key/enter-api-key.component';
import { SettingsComponent } from './settings/settings.component';
import { LoadingComponent } from './loading/loading.component';
import { TrialEndComponent } from './trial-end/trial-end.component';
import { LinkCreatedComponent } from './link-created/link-created.component';
import { LinkGeneratorComponent } from './link-generator/link-generator.component';
import { LinkCreatorComponent } from './link-creator/link-creator.component';
import { ErrorComponent } from './error/error.component';

const routes: Routes = [
  { path: 'app', component: AppComponent },
  { path: 'enterApiKey', component: EnterApiKeyComponent },
  { path: 'settings', component: SettingsComponent },
  { path: 'loading', component: LoadingComponent },
  { path: 'trialEnd', component: TrialEndComponent },
  { path: 'linkCreated', component: LinkCreatedComponent },
  { path: 'linkGenerator', component: LinkGeneratorComponent },
  { path: 'linkCreator', component: LinkCreatorComponent },
  { path: 'error', component: ErrorComponent },
   {path: '', redirectTo: 'loading', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }



