import { Component, OnInit } from '@angular/core';
import { Router } from "@angular/router";
import { NgZone } from '@angular/core';

import { PixelMeService } from '.././pixel-me.service';

@Component({
  selector: 'app-link-created',
  templateUrl: './link-created.component.html',
  styleUrls: ['./link-created.component.css']
})
export class LinkCreatedComponent implements OnInit {

  currentTabUrl: string = '';
  shortedLink: string = '';
  shortedTitle: string = ''; // for "share" buttons

  linkId: string = ''; // for editing link ending (PATCH request)
  linkDomain: string = '';
  linkEnding: string = '';

  loaded: boolean = false;
  edit: boolean = false; // editing link ending mode
  error: boolean = false; // to show error message

  constructor(private router: Router,
    private service: PixelMeService,
    private ngZone: NgZone) { }

  ngOnInit() {

    this.linkDomain = this.service.getSelectedDomain();

    // getting tab url
    this.service.getCurrentTabUrl((data) => {
      this.currentTabUrl = data[0].url;
      
      // getting shorten via API
      this.service.getShortLinkData(this.currentTabUrl, (data => {

        // checking errors
        if (data.error != null) {
        // checking trial
          if (data.error.error_message.startsWith('Trial ended')) {
            this.router.navigate(['trialEnd']);            
          } else {
            this.router.navigate(['error'], { queryParams: { errorMessage: data.error.error_message } });
          }
        }

        this.ngZone.runGuarded(() => {
          this.linkId = data.id;  // for editing link ending
          this.shortedLink = data.shorten;
          this.shortedTitle = data.title;
          this.loaded = true;
          this.copyToClipboard(this.shortedLink);
        });

      }));

    });

  }

  copyToClipboard(text) {
    var textArea = document.createElement("textarea");
    textArea.value = text;
    document.body.appendChild(textArea);

    textArea.select();

    try {
      var successful = document.execCommand('copy');
      var msg = successful ? 'successful' : 'unsuccessful';
      console.log('Copying text command was ' + msg);
    } catch (err) {
      console.log('Oops, unable to copy');
    }

    document.body.removeChild(textArea);
  }

  onShare(url: string) {
    window.open(url, 'popup', 'width=800, height=600');
  }

  onEditSave() {
    // if error was when last attempt
    this.error = false;

    this.service.editShortLink(this.linkId, this.linkEnding, (data) => {

      this.loaded = false;
      // checking trial
      if (data.error != null && data.error.error_message.startsWith('Trial ended')) {
        this.router.navigate(['trialEnd']);
      }

      // checking input errors
      if (data.error != null && data.error.error_message.startsWith('Key already taken for this domain')) {
        this.error = true;
        this.loaded = true;
      } else {
        this.shortedLink = data.shorten;
        this.copyToClipboard(this.shortedLink);
        this.edit = false;
        this.loaded = true;
      }


    })
  }


}



