import { Component } from '@angular/core';
import { Router } from "@angular/router";
import { NgZone } from '@angular/core';

import { PixelMeService } from './pixel-me.service';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {

  currentTabUrl: string = '';

  constructor(private router: Router,
    private service: PixelMeService,
    private ngZone: NgZone) { }

  ngOnInit() { // app - main component - for managing application routers and views

    var apiKey = this.service.getApiKey();
    var pixelsSelcted = this.service.getSelectedPixels();

    // checking if api key entered
    if (apiKey) {

      if (pixelsSelcted == null) {
        this.ngZone.runGuarded(() => {
          this.router.navigate(['linkGenerator']);
        });
      } else {

        this.service.getCurrentTabUrl((data) => {
          this.currentTabUrl = data[0].url;
          if (this.currentTabUrl.startsWith("chrome://extensions/?options")) {
            this.ngZone.runGuarded(() => {
              this.router.navigate(['settings']);
            });
          } else if (this.currentTabUrl.startsWith("chrome://")) {
            this.ngZone.runGuarded(() => {
              this.router.navigate(['linkCreator']); // create new short     
            });
          } else {
            this.ngZone.runGuarded(() => {
              this.router.navigate(['linkCreated']);
            });
          }
        });

      }

    }
    else {
      this.router.navigate(['enterApiKey']);
    }
  }

}



