import { Component, OnInit } from '@angular/core';
import { Router } from "@angular/router";
import { Location } from "@angular/common";

import { User } from './../user';
import { Pixel } from './../pixel';

import { PixelMeService } from '.././pixel-me.service';
declare var $: any;

@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.css']
})
export class SettingsComponent implements OnInit {

  constructor(private router: Router,
    private service: PixelMeService,
    private location: Location) { }

  apiKey: string = "";
  apiKeyOld: string = ""; // To check if user changed his api key

  pixels: Pixel[] = [];
  domains: string[] = [];
  pixelsSelected: Pixel[] = [];
  domainSelected: string = "";

  pixelsLocal: Pixel[] = [];
  domainsLocal: string[] = [];
  pixelsSelectedLocal: Pixel[] = [];
  domainSelectedLocal: string = "";

  pixelsRemote: Pixel[] = [];
  domainsRemote: string[] = [];

  loaded: boolean = false;
  uiInitialized: boolean = false;
  apiKeyValid: boolean = true; // for "invalid API-Key message"
  updated: boolean = false; // for "Preferences updated"

  ngOnInit() {

    // getting local user data
    this.apiKey = this.service.getApiKey();
    this.apiKeyOld = this.apiKey;
    this.pixelsLocal = this.service.getPixels();
    this.domainsLocal = this.service.getDomains();
    this.pixelsSelectedLocal = this.service.getSelectedPixels();
    this.domainSelectedLocal = this.service.getSelectedDomain();

    // getting remote account user data
    this.service.getAccountData((data => {
      if (data != null) {

      // checking errors
      if (data.error != null) {
        // checking trial
        if (data.error.error_message.startsWith('Trial ended')) {
          this.router.navigate(['trialEnd']);            
        } else {
          this.router.navigate(['error'], { queryParams: { errorMessage: data.error.error_message } });
        }
      }

        this.domainsRemote = data.domains;
        this.pixelsRemote = data.pixels;

        // checking if domains or pixels data changed
        if (!this.arraysEqual(this.domainsLocal, this.domainsRemote)) {
          this.domains = this.domainsRemote;
          this.domainSelected = '';
          this.service.setDomains(this.domains);
        }
        if (!this.pixelsArrayEqual(this.pixelsLocal, this.pixelsRemote)) {
          this.pixels = this.pixelsRemote;
          this.pixelsSelected = [];
          this.service.setPixels(this.pixels);
        }

        // Checking domains
        if (this.domainsLocal.length == 0)
          this.domainsLocal = ["pxlme.me"];

        this.domains = this.domainsLocal;
        this.pixels = this.pixelsLocal;
        this.pixelsSelected = this.pixelsSelectedLocal;
        this.domainSelected = this.domainSelectedLocal;

        this.loaded = true;
      }
    }));
  }

  // Called after the ngAfterViewInit and every subsequent ngAfterContentChecked().
  ngAfterViewChecked() {
    // Initialization of ui elements after loading content
    if (this.loaded == true && this.uiInitialized == false) {
      this.initializeDropdown();
      this.uiInitialized = true;
    }
  }

  onUpdate() {
    // clean all validation messages
    this.apiKeyValid = true;
    this.updated = false;

    // set selected values
    this.service.setSelectedDomain(this.domainSelected);
    this.service.setSelectedPixels(this.pixelsSelected);

    // check api key
    if (this.apiKey != this.apiKeyOld) {
      this.service.setApiKey(this.apiKey);
      this.service.getAccountResponseStatus((data => {
        if (data == null) {
          this.apiKeyValid = false;
          this.service.setApiKey(this.apiKeyOld);
        } else if (data == 401) {
          this.apiKeyValid = false;
          this.service.setApiKey(this.apiKeyOld);
        } else {
          this.updated = true;
        }
      }));
    } else {
      this.updated = true;
    }

  }

  // Handling domain selecting in dropdown
  onDomainSelected(event: any) {
    this.domainSelected = event;
  }

  // Handling pixel selecting in dropdown
  onPixelSelected(pixel: Pixel) {
    var idx = this.pixelsArrayIndex(this.pixelsSelected, pixel);

    // Is currently selected
    if (idx > -1) {
      this.pixelsSelected.splice(idx, 1);
    }
    // Is newly selected
    else {
      this.pixelsSelected.push(pixel);
    }
  };

  // when called, will initialize all dropdown and all checkboxes.
  initializeDropdown() {
    $('.ui.selection.dropdown').dropdown();
    $('.ui.checkbox').checkbox();
    $('#n').dropdown({ action: 'nothing' });
  }

  arraysEqual(arr1, arr2) {
    if (arr1.length !== arr2.length)
      return false;
    for (var i = arr1.length; i--;) {
      if (arr1[i] !== arr2[i])
        return false;
    }
    return true;
  }

  pixelsArrayEqual(arr1: Pixel[], arr2: Pixel[]) {
    return this.service.pixelsArrayEqual(arr1, arr2);
  }

  pixelsArrayContains(arr: Pixel[], px: Pixel) {
    return this.service.pixelsArrayContains(arr, px);
  }

  pixelsArrayIndex(arr: Pixel[], px: Pixel): number {
    return this.service.pixelsArrayIndex(arr, px);
  }


}



