import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';

import {SuiModule} from 'ng2-semantic-ui';

import { AppComponent } from './app.component';
import { EnterApiKeyComponent } from './enter-api-key/enter-api-key.component';
import { AppRoutingModule } from './/app-routing.module';
import { SettingsComponent } from './settings/settings.component';
import { TrialEndComponent } from './trial-end/trial-end.component';
import { LoadingComponent } from './loading/loading.component';
import { LinkCreatedComponent } from './link-created/link-created.component';
import { LinkGeneratorComponent } from './link-generator/link-generator.component';
import { PixelMeService } from './pixel-me.service';
import { LinkCreatorComponent } from './link-creator/link-creator.component';
import { ErrorComponent } from './error/error.component';


@NgModule({
  declarations: [
    AppComponent,
    EnterApiKeyComponent,
    SettingsComponent,
    TrialEndComponent,
    LoadingComponent,
    LinkCreatedComponent,
    LinkGeneratorComponent,
    LinkCreatorComponent,
    ErrorComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    AppRoutingModule,
    SuiModule,
    HttpClientModule
  ],
  providers: [PixelMeService],
  bootstrap: [AppComponent]
})
export class AppModule { }



