import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';


import { User } from './user';
import { Pixel } from './pixel';
import { ShortLink } from './short-link';

import 'rxjs/Rx';

@Injectable()
export class PixelMeService {

  apikey: string = "";

  urlAccount: 'https://api.pixelme.me/accounts';
  URL_REDIRECT: 'https://api.pixelme.me/redirects';

  constructor(private http: HttpClient) {
  }

  setApiKey(apiKey: string) {
    // chrome.storage.sync.set({'apikey': ApiKey}, function() {
    //   console.log('set api key: ' + ApiKey)
    // });
    this.apikey = apiKey;

    localStorage.setItem('apikey', apiKey);

  }

  getApiKey(): string {
    var apiKey: string;
    //chrome.storage.sync.get("apikey", callback);
    apiKey = this.apikey;
    apiKey = localStorage.getItem('apikey');
    return apiKey;
  }

  setPixels(pixels: Pixel[]) {
    localStorage.setItem('pixels', JSON.stringify(pixels));
  }

  getPixels(): Pixel[] {
    return JSON.parse(localStorage.getItem('pixels'));
  }

  setSelectedPixels(selectedPixels: Pixel[]) {
    localStorage.setItem('selectedpixels', JSON.stringify(selectedPixels));
  }

  getSelectedPixels(): Pixel[] {
    return JSON.parse(localStorage.getItem('selectedpixels'));
  }

  setDomains(domains: string[]) {
    localStorage.setItem('domains', JSON.stringify(domains));
  }

  getDomains(): string[] {
    let domains = JSON.parse(localStorage.getItem('domains'));
    if (domains.length == 0)
      return ["pxlme.me"];
    else
      return domains;
  }

  setSelectedDomain(domain: string) {
    localStorage.setItem('selecteddomain', JSON.stringify(domain));
  }

  getSelectedDomain(): string {
    let selected = localStorage.getItem('selecteddomain');
    if (selected == 'undefined')
      return "pxlme.me";
    else
      return JSON.parse(localStorage.getItem('selecteddomain'));
  }

  getAccountResponseStatus(callback): any {
    return this.http.get('https://api.pixelme.me/accounts', {
      headers: new HttpHeaders({
        'Access-Control-Allow-Origin': '*',
        'Authorization': 'Bearer ' + this.getApiKey()
      }),
      observe: "response"
    }).subscribe(
      res => callback(res.status),
      err => callback(err.status)
      );
  }

  getAccountData(callback): any {
    return this.http.get<User>('https://api.pixelme.me/accounts', {
      headers: new HttpHeaders({
        'Access-Control-Allow-Origin': '*',
        'Authorization': 'Bearer ' + this.getApiKey()
      })
    }).subscribe(
      res => callback(res),
      err => callback(err)
      );
  }


  // --------------------- working with shorteners -------------------------------------------

  getSelectedPixelsIds(): string[] {
    var sPixels = this.getSelectedPixels();
    var result: string[] = [];
    if (sPixels) {
      for (var i = 0; i < sPixels.length; i++) {
        result.push(sPixels[i].id);
      }
    }
    return result;
  }

  getCurrentTabUrl(callback) { //Take a callback
    chrome.tabs.query({ active: true, currentWindow: true }, function (tab) {
      callback(tab); //call the callback with argument
    });
  };


  getShortLinkData(urlToShort, callback): any {
    var body = {
      url: urlToShort,
      pixels_ids: this.getSelectedPixelsIds()
    }

    const headers1 = new HttpHeaders();
    headers1.set('Access-Control-Allow-Origin', '*');
    headers1.append('Authorization', 'Bearer ' + this.getApiKey());

    return this.http.post('https://api.pixelme.me/redirects', JSON.stringify(body), {
      headers: new HttpHeaders().
        set('Authorization', 'Bearer ' + this.getApiKey()).
        append('Access-Control-Allow-Origin', '*'),
    }).subscribe(
      res => callback(res),
      err => callback(err)
      );
  }

  editShortLink(linkId, ending, callback): any {
    var body = {
      key: ending,
      pixels_ids: this.getSelectedPixelsIds()
    }

    const headers1 = new HttpHeaders();
    headers1.set('Access-Control-Allow-Origin', '*');
    headers1.append('Authorization', 'Bearer ' + this.getApiKey());

    return this.http.patch('https://api.pixelme.me/redirects/' + linkId, JSON.stringify(body), {
      headers: new HttpHeaders().
        set('Authorization', 'Bearer ' + this.getApiKey()).
        append('Access-Control-Allow-Origin', '*'),
    }).subscribe(
      res => callback(res),
      err => callback(err)
      );
  }


  // ----------------------------------- Some additional methods ---------------------------------

  pixelsArrayEqual(arr1: Pixel[], arr2: Pixel[]) {
    try {
      if (arr1.length !== arr2.length)
        return false;
      for (var i = arr1.length; i--;) {
        if ((arr1[i].created_at !== arr2[i].created_at) ||
          (arr1[i].id !== arr2[i].id) ||
          (arr1[i].key !== arr2[i].key) ||
          (arr1[i].name !== arr2[i].name) ||
          (arr1[i].provider_key !== arr2[i].provider_key))
          return false;
      }
      return true;
    }
    catch (Error) {
      return false;
    }
  }


  pixelsArrayContains(arr: Pixel[], px: Pixel) {
    try {
      if (!arr)
        return false;
      for (var i = arr.length; i--;) {
        if ((arr[i].created_at == px.created_at) &&
          (arr[i].id == px.id) &&
          (arr[i].key == px.key) &&
          (arr[i].name == px.name) &&
          (arr[i].provider_key == px.provider_key))
          return true;
      }
      return false;
    }
    catch (Error) {
      return false;
    }
  }

  pixelsArrayIndex(arr: Pixel[], px: Pixel): number {
    try {
      if (!arr)
        -1;
      for (var i = arr.length; i--;) {
        if ((arr[i].created_at == px.created_at) &&
          (arr[i].id == px.id) &&
          (arr[i].key == px.key) &&
          (arr[i].name == px.name) &&
          (arr[i].provider_key == px.provider_key))
          return i;
      }
      return -1;
    }
    catch (Error) {
      return -1;
    }
  }

}


